.container {
  width: 960px;
  max-width: 960px;
  margin: 0px auto;
  padding: 0px 15px; }
  @media (max-width: 960px) {
    .container {
      width: 100vw; } }

.pull-right {
  float: right; }

.pull-left {
  float: left; }

.login-form {
  margin: 0px auto; }
  @media (min-width: 960px) {
    .login-form {
      width: 30%; } }

.form-item {
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin-bottom: 10px; }
  .form-item label {
    font-weight: 400;
    flex-basis: content;
    line-height: 30px; }
    .form-item label:after {
      content: ":";
      display: inline-block;
      margin: 0px 5px; }
  .form-item input {
    height: 30px;
    font-size: 13px; }

body {
  color: black;
  display: flex;
  margin: 0;
  padding: 0;
  flex-direction: column; }

.welcome-page {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  text-align: center; }
  .welcome-page a {
    color: #333;
    font-weight: bold; }
